	module.exports = {"data":{"site":{"siteMetadata":{"startDate":"2018-06-06","lessons":8,"noClass":[4]}},"markdownRemark":{"html":"<p>This week we will be talking about floating. How to align images, build gallery and simple navigation.</p>\n<p><span class=\"more\"></span></p>\n<p>Since Everything on in web design is a rectangular box we need to know how to size that box. The way that CSS calculates the size of a box is not what you expect. Rather than set the size of the box based on the outer most elements <code class=\"language-text\">border</code> or <code class=\"language-text\">padding</code> the width is set baed on the content.</p>\n<h3>Setting a width</h3>\n<p>To set the width of an element you take the desired width and subtract the <code class=\"language-text\">padding-left</code>, <code class=\"language-text\">padding-right</code>, <code class=\"language-text\">border-left-width</code> and <code class=\"language-text\">border-right-width</code>. If you need to maintain the <code class=\"language-text\">margin</code> you will need to subtract the <code class=\"language-text\">margin-left</code> and <code class=\"language-text\">margin-right</code>.</p>\n<h3>Example</h3>\n<p>If we have an element that needs to fit in to a space that is <code class=\"language-text\">400px</code> and it has <code class=\"language-text\">padding: 20px</code> and <code class=\"language-text\">border: 5px solid black</code> the calculation to find the size would look like this.</p>\n<div class=\"gatsby-highlight\" data-language=\"html\">\n      <pre class=\"language-html\"><code class=\"language-html\">400px - 20px -20px - 5px - 5px = 350px</code></pre>\n      </div>\n<h3>Example</h3>\n<iframe height='800' scrolling='no' title='Box Model' src='//codepen.io/danhahn/embed/MoQNbO/?height=734&theme-id=light&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='https://codepen.io/danhahn/pen/MoQNbO/'>Box Model</a> by Dan Hahn (<a href='https://codepen.io/danhahn'>@danhahn</a>) on <a href='https://codepen.io'>CodePen</a>.\n</iframe>\n<h2>Box Sizing</h2>\n<p>In CSS, by default, the width and height you assign to an element is applied only to the element's content box. If the element has any border or padding, this is then added to the width and height to arrive at the size of the box that's rendered on the screen. This means that when you set width and height you have to adjust the value you give to allow for any border or padding that may be added. This is especially tricky when implementing a responsive design.</p>\n<p>The <code class=\"language-text\">box-sizing</code>property can be used to adjust this behavior:</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">.example</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">box-sizing</span><span class=\"token punctuation\">:</span> border-box<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<ul>\n<li><code class=\"language-text\">content-box</code> is the default, and gives you the default CSS box-sizing behavior. If you set an element's width to 100 pixels, then the element's content box will be 100 pixels wide, and the width of any border or padding will be added to the final rendered width.</li>\n<li><code class=\"language-text\">border-box</code> tells the browser to account for any border and padding in the value you specify for width and height. If you set an element's width to 100 pixels, that 100 pixels will include any border or padding you added, and the content box will shrink to absorb that extra width. This typically makes it much easier to size elements.</li>\n</ul>\n<h2>Apply to all elements</h2>\n<p>The <code class=\"language-text\">*</code> selector will apply a style to all elements. <code class=\"language-text\">box-sizing</code> is a great example of time to use it.</p>\n<div class=\"gatsby-highlight\" data-language=\"css\">\n      <pre class=\"language-css\"><code class=\"language-css\"><span class=\"token selector\">*</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">box-sizing</span><span class=\"token punctuation\">:</span> border-box<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Now all elements on the page will size based on a <code class=\"language-text\">border-box</code>.</p>","frontmatter":{"lesson":"CSS Box Model","title":"Lesson 5","lessonId":5,"downloads":{"Download_Stater_File":{"file":{"publicURL":"/static/week5-37dc1f325118acab80096e3aadfb02b4.zip"}},"Homework_Files":null}}},"allMarkdownRemark":{"edges":[{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/5/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 5","lesson":"CSS Box Model"},"fields":{"slug":"/5/index.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/5/pseudo.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 5","lesson":"Pseudo class"},"fields":{"slug":"/5/pseudo.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/5/floats.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 5","lesson":"Floats"},"fields":{"slug":"/5/floats.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/5/clear-fix.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 5","lesson":"Clear fix"},"fields":{"slug":"/5/clear-fix.html"}}}]}},"pathContext":{"slug":"/5/index.html","title":"Lesson 5"}}

